.title-home {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 90vh;
  justify-content: center;
}
  
.title-page {
  color: $cleanWhite;
  font-family: $amatic;
  font-size: 10vh;
  font-weight: 400;
  padding: 0 3vh 0 3vh;
  text-align: center;
}

.arrow-down {
  margin-top: 15vh;
  width: 12vh;
  cursor: pointer;
  animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

.arrow-down:hover {
  animation: pulse 0.3s cubic-bezier(0.4, 0, 0.6, 1) infinite;
}

@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: .5;
  }
}

@media screen and (max-width: 540px) {
  .arrow-down {
    margin-top: 10vh;
    width: 9vh;
  }

  .title-home {
    height: 100vh;
  }

  .title-page {
    font-size: 7.5vh;
  }
}