.filter-img {
  width: 6vh;
}

.title-filter {
  font-size: 3vh;
  line-height: 4vh;
  font-family: $andika;
  color: $cleanWhite;
}

.filter {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background-color: $blackOpacity8;
  padding: 1.5vh 3vh;
  margin: 1.5vh 0;
}

.list-filters {
  display: flex;
}

.hidden-filters {
  display: none;
}

.grid-filters {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  width: 100%;
  gap: 1.5vh;
}

.item-filter{
  height: 24vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-position: center;
  padding: 1.5vh;
  background-image: url('../../images/wallpapers/ceu_noturno.jpeg');
  background-size: cover;
  cursor: pointer;

  p {
    font-family: $andika;
    color: $cleanWhite;
    font-size: 2.4vh;
    font-weight: 600;
    width: 100%;
    text-align: center;
    line-height: 1.3rem;
  }
}

.item-filter:hover{
  border: 1px solid $cleanWhite;
}

.item-filter-not-selected {
  background-image: url('../../images/wallpapers/ceu_noturno.jpeg');
}

.item-filter-selected{
  background-image: url('../../images/wallpapers/moon.jpg');
}

.image-filter {
  height: 80%;
  object-fit: contain;
}

@media screen and (max-width: 700px) {
  .grid-filters {
    grid-template-columns: 1fr;
  }
}